  sendWhatsapp = (record, index) => {
    var actuallist = [...this.state.actuallist];
    var flightnumber = record.flightnumber;
    var tripid = record.tripid;
    var followups = record.followups;
    followups = followups + 1;
    var followupformat = new Date();
    var followupday = followupformat.getDay();
    var followupmonth = followupformat.getMonth();
    var followupyear = followupformat.getFullYear();
    var followuptimestamp = followupformat.getTime();
    //var followupdate = followupday+' / '+followupmonth+' / '+followupyear;
    var followupdate = followupformat.toDateString();

    var indexFlight = this.findWithAttr(actuallist, 'flightnumber', flightnumber);
    actuallist[indexFlight].trips[index].followups = followups;
    actuallist[indexFlight].trips[index].followupdate = followupdate;


    ///save to firebase

    database.ref('beckbag').child('carriertrips').child('interested').child(tripid).update({
      followups: followups,
      followupdate: followupdate,
      followuptimestamp: followuptimestamp
    });

    this.setState({
          actuallist: actuallist,
        });

    this.openNotificationWhatsapp();
  }

