        <Helmet title="Add Package | BeckBags"
          meta={[
            { name: 'description', content: 'Travel Meets Logistics' },
            { property: 'og:description', content: 'Travel Meets Logistics' },
            { property: 'og:title', content: 'Pending Packages | BeckBags' },
            { property: 'og:image', content: 'https://firebasestorage.googleapis.com/v0/b/beckfriends-2-a4131.appspot.com/o/beckicn.jpg?alt=media&token=f0384655-505a-4fdf-af5c-b01f5bb7198a' },
            { property: 'og:type', content: 'website' },
            { property: 'og:site_name', content: 'BeckBags' },
            { name: 'theme-color', content: '#ffffff' },
            { name: 'apple-mobile-web-app-status-bar-style', content: '#ffffff' },
            { name: 'msapplication-navbutton-color', content: '#ffffff' },
          ]} />
          <Row>
            <Col md="12">
              <Card className="customcard" style={{ backgroundColor: 'transparent' }}>
                <CardHeader>
                  <CardTitle tag="h4" style={{ fontSize: '1.5em', fontWeight: '800', marginBottom: '25px' }}>Add Order Details</CardTitle>
                </CardHeader>
                <CardBody style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                 <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
                 <Select
    showSearch
    style={{ width: '60%', color: 'white' }}
    placeholder="Pickup City"
    optionFilterProp="children"
    onChange={this.onChangePickup}
    onFocus={this.onFocus}
    onBlur={this.onBlur}
    onSearch={this.onSearch}
    filterOption={(input, option) =>
      option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
    }
  >
    <Option value="Mumbai">Mumbai</Option>
    <Option value="New Delhi">New Delhi</Option>
    <Option value="New York">New York</Option>
    <Option value="Bangalore">Bangalore</Option>
    <Option value="Chicago">Chicago</Option>
    <Option value="London">London</Option>
    <Option value="Singapore">Singapore</Option>
    <Option value="Dubai">Dubai</Option>
  </Select>
  <Select
    showSearch
    style={{ width: '60%', marginTop: '20px', marginBottom: '20px', color: 'white' }}
    placeholder="Delivery City"
    optionFilterProp="children"
    onChange={this.onChangeDelivery}
    onFocus={this.onFocus}
    onBlur={this.onBlur}
    onSearch={this.onSearch}
    filterOption={(input, option) =>
      option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
    }
  >
    <Option value="Mumbai">Mumbai</Option>
    <Option value="New Delhi">New Delhi</Option>
    <Option value="New York">New York</Option>
    <Option value="Bangalore">Bangalore</Option>
    <Option value="Chicago">Chicago</Option>
    <Option value="London">London</Option>
    <Option value="Singapore">Singapore</Option>
    <Option value="Dubai">Dubai</Option>
  </Select>
  </div>
      <Button type="primary" onClick={this.openCalendar} style={{ color: '#ffffff', backgroundColor: 'transparent', borderColor: 'transparent', borderBottom: '1px solid white', width: '60%', marginBottom: '20px' }}>
      {this.state.deliverydateshow}
    </Button>       
        {this.state.showCalendar && <Calendar
        date={new Date()}
        onChange={this.handleSelect}
      />}
      <Input placeholder="Enter Weight" onChange={this.weightChange} style={{ width: '60%', color: 'white', backgroundColor: 'transparent', textAlign: 'center', borderColor: 'transparent', borderBottom: '1px solid white', marginBottom: '20px' }}/>
      <Input placeholder="Enter No of Packages" onChange={this.numberChange} style={{ width: '60%', color: 'white', backgroundColor: 'transparent', textAlign: 'center', borderColor: 'transparent', borderBottom: '1px solid white', marginBottom: '20px' }}/>
      <Input placeholder="Describe Content of Packages" onChange={this.contentChange} style={{ width: '60%', color: 'white', backgroundColor: 'transparent', textAlign: 'center', borderColor: 'transparent', borderBottom: '1px solid white', marginBottom: '20px' }}/>
      <Input placeholder="Value of Packages in $" onChange={this.valueChange} style={{ width: '60%', color: 'white', backgroundColor: 'transparent', textAlign: 'center', borderColor: 'transparent', borderBottom: '1px solid white', marginBottom: '20px' }}/>
      <ImageUploader
        withIcon={true}
        buttonText="Upload images"
        onChange={this.onDrop}
        imgExtension={[".jpg", ".gif", ".png", ".gif"]}
        maxFileSize={5242880}
        withPreview={true}
        style={{ width: '50%' }}
      />
      <Button type="primary" onClick={this.addOrder} style={{ color: '#000', fontWeight: '600', backgroundColor: 'white', borderColor: 'transparent', borderBottom: '1px solid white', width: '60%', marginBottom: '20px' }}>
      Add Order
    </Button> 
                </CardBody>
              </Card>
            </Col>
