    componentDidMount() {
  var countinside = 0;
  var pending = 0;
  var ongoing = 0;
  var matched = 0;
  var total = 0;
  var completed = 0;

  var that = this;

  //////checking if user is merchant or TA

  firebaseinit.auth().onAuthStateChanged((user) => {
  if (user) {
    
    var uid = user.uid;
    if(uid === '9TycnH0HHNNxEoVEVA6KR40Pr2H2')
    {
      uid = '4de0e39c-d263-427f-a98f-2076a034b6c5';
    }
    /// check if user is part of merchants or travel partners

    database.ref('beckbag').child('merchants').child(uid)
              .once('value')
              .then((snapshot) => {
                
                if(snapshot.val() != null)
                {
                  that.setState({
                    merchantDashboard: true,
                    merchantPackages: true,
                    merchantNotifications: true,
                    merchantMessaging: true,
                    merchantImage: true,
                    merchantRoles: true,
                  });
                }
              });

    database.ref('beckbag').child('carrier').child(uid)
              .once('value')
              .then((snapshot) => {
                
                if(snapshot.val() != null)
                {
                 that.setState({
                    travelOverview: true,
                    travelTrips: true,
                    travelNotifications: true,
                    travelMessaging: true,
                    travelInterested: true,
                    travelImage: true,
                    travelRoles: true,
                  });
                }
              });          
  } else {
    // User not logged in or has just logged out.
  }
});


  ///////// loading notifications

  database.ref('beckbag').child('merchants').child('4de0e39c-d263-427f-a98f-2076a034b6c5').child('notifications')
        .limitToLast(8)
        .orderByChild('timestamp')
        .once('value')
        .then((snapshot) => {
          var count = snapshot.numChildren();
          snapshot.forEach((chSnapshot) => {
            var key = chSnapshot.key;
            
            var notification = chSnapshot.val();
            
            if (notification.show == undefined){
              notification.show = true;
            }
            if((notification.type == 'Ongoing') && (notification.show))
              {
                ongoing++;
                ongoingkeys.push(key);
                total++;
              }
            if((notification.type == 'Add') && (notification.show))
              {
                pending++;
                pendingkeys.push(key);
                total++;
              }
            if((notification.type == 'AcceptFee') && (notification.show))
              {
                matched++;
                matchedkeys.push(key);
                total++;
              }    
            if((notification.type == 'OngIssue') && (notification.show))
              {
                ongoing++;
                ongoingkeys.push(key);
                total++;
              } 
            if((notification.type == 'ComPayment') && (notification.show))
              {
                completed++;
                completedkeys.push(key);
                total++;
              }
            if((notification.type == 'ComIssue') && (notification.show))
              {
                completed++;
                completedkeys.push(key);
                total++;
              }
              if((notification.type == 'MerchantShipped') && (notification.show))
              {
                ongoing++;
                ongoingkeys.push(key);
                total++;
              }       
            countinside++;
            //notifications.push(notification);
            if( countinside === count )
            { 
              if(pending == 0){
                pending = '';
              }
              if(matched == 0){
                matched = '';
              }
              if(ongoing == 0){
                ongoing = '';
              }
              if(completed == 0){
                completed = '';
              }
              if(total == 0)
              {
                total = '';
              }
              that.setState({
                pending: pending,
                ongoing: ongoing,
                matched: matched,
                completed: completed,
                total: total
            });
            }
            
    });
  });   



  this.setState({

  });

  };

  onOpenChange = openKeys => {
    const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
    if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      this.setState({ openKeys });
    } else {
      this.setState({
        openKeys: latestOpenKey ? [latestOpenKey] : [],
      });
    }
  };

